/*** IMPORTS FROM imports-loader ***/
var define = false;
var __VERSION__ = "4.3.2";

import { stripIndent } from 'common-tags'

export default function(widgetName, values) {
  return stripIndent`
    let { ${widgetName} } = ReactWidgets;

    let widgets = (
      <div>
        ${values
          .map(
            v => `
        <${widgetName} defaultValue={${v}} />`
          )
          .join('')
          .trim()}
      </div>
    )

    render(widgets);
  `
}

